"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Header from "@/components/Header";
import Footer from "@/components/Footer";

const dosenTabs = [
  { id: "overview", label: "Overview", icon: "ðŸ“Š" },
  { id: "penghargaan", label: "Penghargaan", icon: "ðŸ†" },
  { id: "penelitian", label: "Penelitian", icon: "ðŸ”¬" },
  { id: "pengabdian", label: "Pengabdian", icon: "ðŸ¤" },
  { id: "publikasi-buku", label: "Publikasi Buku", icon: "ðŸ“š" },
  { id: "publikasi-jurnal", label: "Publikasi Jurnal", icon: "ðŸ“„" },
  { id: "riset-hki", label: "Riset HKI", icon: "ðŸ’¡" },
  { id: "riset-non-hki", label: "Riset Non HKI", icon: "ðŸ”" },
];

const DosenDetailPage = ({ params }: { params: { nidn: string } }) => {
  const router = useRouter();
  const { nidn } = params;

  const [activeTab, setActiveTab] = useState("overview");
  const [dosenData, setDosenData] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchDosen = async () => {
    try {
      setLoading(true);
      setError(null);

      const res = await fetch(`/api/dosen/${nidn}`);

      if (!res.ok) {
        throw new Error(`HTTP error! status: ${res.status}`);
      }

      const json = await res.json();
      if (json.success) {
        setDosenData(json.data);
        setError(null);
      } else {
        setError(json.message || "Dosen not found");
      }
    } catch (err) {
      console.error('Fetch error:', err);
      if (err instanceof Error) {
        setError(`Failed to fetch dosen data: ${err.message}`);
      } else {
        setError("Failed to fetch dosen data: Network error");
      }
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDosen();
  }, [nidn]);

  if (loading) {
    return (
      <>
        <Header isLoggedIn={true} onLogout={() => router.push("/")} />
        <main className="p-8">
          <p>Loading dosen data...</p>
        </main>
        <Footer />
      </>
    );
  }

  if (error) {
    return (
      <>
        <Header isLoggedIn={true} onLogout={() => router.push("/")} />
        <main className="p-8">
          <p className="text-red-600">{error}</p>
          <button
            onClick={() => router.push("/admin/dashboard")}
            className="mt-4 px-4 py-2 bg-gray-500 text-white rounded hover:bg-gray-600"
          >
            Back to Dashboard
          </button>
        </main>
        <Footer />
      </>
    );
  }

  return (
    <>
      <Header isLoggedIn={true} onLogout={() => router.push("/")} />
      <main className="p-8">
        <h1 className="text-3xl font-bold mb-6 text-red-600 font-roboto">
          Dosen Detail: {dosenData.nama}
        </h1>
        <button
          onClick={() => router.push("/admin/dashboard")}
          className="mb-6 px-4 py-2 bg-gray-500 text-white rounded hover:bg-gray-600"
        >
          Back to Dashboard
        </button>

        <div className="mb-4 flex items-center space-x-4 border-b border-gray-300 pb-2">
          {dosenTabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex items-center space-x-2 px-4 py-2 font-roboto text-sm rounded-t-lg transition-colors ${
                activeTab === tab.id
                  ? "bg-red-500 text-white"
                  : "bg-gray-200 text-gray-700 hover:bg-red-100"
              }`}
            >
              <span>{tab.icon}</span>
              <span>{tab.label}</span>
            </button>
          ))}
        </div>

        {activeTab === "overview" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Overview</h2>
            <p>Nama: {dosenData.nama}</p>
            <p>NIDN: {dosenData.nidn}</p>
            <p>Status: {dosenData.status}</p>
            <p>Pangkat: {dosenData.pangkat}</p>
            <p>Aktif: {dosenData.isActive ? "Ya" : "Tidak"}</p>
            <p>Deskripsi Kepakaran: {dosenData.deskripsiKepakaran}</p>
          </section>
        )}

        {activeTab === "penghargaan" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Penghargaan</h2>
            {dosenData.penghargaan && dosenData.penghargaan.length > 0 ? (
              <div className="space-y-4">
                {dosenData.penghargaan.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.namaPenghargaan}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.pemberi}</p>
                    <p className="text-sm">{item.deskripsi}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data penghargaan.</p>
            )}
          </section>
        )}

        {activeTab === "penelitian" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Penelitian</h2>
            {dosenData.penelitian && dosenData.penelitian.length > 0 ? (
              <div className="space-y-4">
                {dosenData.penelitian.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.sumberDana}</p>
                    <p className="text-sm">{item.deskripsi}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data penelitian.</p>
            )}
          </section>
        )}

        {activeTab === "pengabdian" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Pengabdian</h2>
            {dosenData.pengabdian && dosenData.pengabdian.length > 0 ? (
              <div className="space-y-4">
                {dosenData.pengabdian.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.lokasi}</p>
                    <p className="text-sm">{item.deskripsi}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data pengabdian.</p>
            )}
          </section>
        )}

        {activeTab === "publikasi-buku" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Publikasi Buku</h2>
            {dosenData.publikasiBuku && dosenData.publikasiBuku.length > 0 ? (
              <div className="space-y-4">
                {dosenData.publikasiBuku.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.penerbit}</p>
                    <p className="text-sm">ISBN: {item.isbn}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data publikasi buku.</p>
            )}
          </section>
        )}

        {activeTab === "publikasi-jurnal" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Publikasi Jurnal</h2>
            {dosenData.publikasiJurnal && dosenData.publikasiJurnal.length > 0 ? (
              <div className="space-y-4">
                {dosenData.publikasiJurnal.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.namaJurnal}</p>
                    <p className="text-sm">Volume: {item.volume}, Issue: {item.issue}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data publikasi jurnal.</p>
            )}
          </section>
        )}

        {activeTab === "riset-hki" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Riset HKI</h2>
            {dosenData.risetHKI && dosenData.risetHKI.length > 0 ? (
              <div className="space-y-4">
                {dosenData.risetHKI.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.jenisHKI}</p>
                    <p className="text-sm">Nomor: {item.nomorHKI}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data riset HKI.</p>
            )}
          </section>
        )}

        {activeTab === "riset-non-hki" && (
          <section className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Riset Non HKI</h2>
            {dosenData.risetNonHKI && dosenData.risetNonHKI.length > 0 ? (
              <div className="space-y-4">
                {dosenData.risetNonHKI.map((item: any, index: number) => (
                  <div key={index} className="border-b pb-4">
                    <h3 className="font-medium">{item.judul}</h3>
                    <p className="text-sm text-gray-600">{item.tahun} - {item.sumberDana}</p>
                    <p className="text-sm">{item.deskripsi}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p>Tidak ada data riset non HKI.</p>
            )}
          </section>
        )}
      </main>
      <Footer />
    </>
  );
};

export default DosenDetailPage;
